<template>
    <div>
        <Header title="管理员"></Header>
        <div id="table">
            <table>
                <tr class="title_manages">
                    <th>序号</th>
                    <th>平台名称</th>
                    <th>平台邮箱</th>
                    <th>备案网址</th>
                    <th>备案域名</th>
                    <th>备案号</th>
                    <th>经营范围</th>
                    <th>审批</th>
                </tr>

                <!-- <tr> -->
                <template v-for="(item, index) in newsList">
                    <tr class="trData">
                        <td width="5%">{{ item.id }}</td>
                        <td width="15">{{ item.name }}</td>
                        <td width="15%">{{ item.email }}</td>
                        <td width="10%">{{ item.site }}</td>
                        <td width="15%">{{ item.domainname }}</td>
                        <td width="10%">{{ item.recordno }}</td>
                        <td width="15%">{{ item.businessscope }}</td>
                        <td width="15%">
                            <span @click="accept(item.id)" class="accept">通过</span>
                            <span @click="refuse(item.id)" class="refuse">拒绝</span>
                        </td>
                    </tr>
                </template>
                <!-- </tr> -->
            </table>
          <div class="mana_exit">
            <input style="margin-left: 50%; width: 50px" type="button" @click="mana_exit" value="退出"></input>
          </div>
        </div>

<!--        <Footer></Footer>-->
    </div>
</template>

<script>
    import Header from '../../components/Header'
    import Footer from '../../components/Footer'
    export default {
        name: 'manages',
        components: {
            Header,
            Footer,
        },
        data: function () {
            return {
                newsList: [],
                userInfo: {
                    id:undefined,
                    name: undefined,
                    email: undefined,
                    site: undefined,
                    domainname: undefined,
                    recordno: undefined,
                    businessscope: undefined,
                    password: undefined,
                    lockedstate: undefined
                },
            }
        },
        created() {
            this.getList();
        },
        methods: {

          mana_exit: function (){
            this.$router.push({ path: '/index' });
          },

            getList(){
               this.$request({
                    url:'/user/info',
                    method:'get',
                }).then((res)=>{
                    this.newsList=res.data
                })
            },
            accept(id){//同意
                this.userInfo.lockedstate=1
                this.userInfo.id=id
                this.$request({
                    url:'/user/edit',
                    method:'post',
                    data: this.userInfo
                }).then((res)=>{
                    this.getList();
                    this.msgSuccess("操作成功");
                })

            },
            refuse(id){//拒绝
                this.userInfo.lockedstate=2
                this.userInfo.id=id
                this.$request({
                    url:'/user/edit',
                    method:'post',
                    data: this.userInfo
                }).then((res)=>{
                    this.getList();
                    this.msgSuccess("操作成功");
                })

            },
        }
    }
</script>

<style scoped>

    #table {
        padding: 0 10px;
        width: 82%;
        margin-left: auto;
        margin-right: auto;
    }

    .title_manages th{
        padding: 10px;
    }

    .trData td{
        padding: 15px;
    }

    input {
        border: 1px solid #008cd5;
        padding: 5px;
        border-radius: 3px;
        /* 棱角变圆 */
        margin-right: 15px;
    }

    #table table {
        width: 100%;
        font-size: 15px;
        border: 1px solid #eee;
        text-align: center;
        border-collapse: collapse;
    }

    table th {
        background-color: #f5f5f5;
        border-right: 1px solid #eee;
        font-size: 20px;
    }

    /*
        #fff
        #f5f5f5
        #eee
        颜色深度依次递增
     */

    table td {
        padding: 10px;
        border-bottom: 1px solid #eee;
        border-right: 1px solid #eee;
    }

    .refuse {
        color: red;
    }

    .accept {
        color: #008cd5;
    }

    tr td span {
        cursor: pointer;
        /*  指定鼠标指引 */
        margin: 10px;
    }


    .title span {
        float: right;
        cursor: pointer;
    }


    .content input {
        width: 270px;
        margin-bottom: 15px;
    }

    .mana_exit {
      margin: 30px auto 20px auto;
    }

    .mana_exit input {
      width: 50px;
      letter-spacing: 3px;
      font-size: 17px;
    }

</style>



